@keyframes grow-in-vertical {
  0% {
    transform: scaleY(0);
  }
  100% {
    transform: scaleY(1);
  }
}
.grow-in-vertical {
  animation: grow-in-vertical 0.3s;
}
@keyframes shrink-out-vertical {
  0% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(0);
  }
}
.shrink-out-vertical {
  animation: shrink-out-vertical 0.15s;
}
@keyframes scale-in {
  0% {
    scale: 0;
  }
  100% {
    scale: 1;
  }
}
.scale-in {
  animation: scale-in 0.3s;
}
@keyframes scale-out {
  0% {
    scale: 1;
  }
  100% {
    scale: 0;
  }
}
.scale-out {
  animation: scale-out 0.3s;
}
button {
  background-color: rgba(50, 50, 50, 0.7);
  color: white;
  outline: none;
  border: 2px solid #969696;
  width: 100%;
  border-radius: 2px;
  margin: 5px;
}
button:hover {
  background-color: rgba(76, 76, 76, 0.7);
}
button:focus {
  background-color: rgba(76, 76, 76, 0.7);
  border-color: #ffffff;
}
button:active {
  background-color: rgba(101, 101, 101, 0.7);
}
button.epic {
  background-color: rgba(146, 107, 214, 0.7);
  border-color: #c8c8c8;
}
button.epic:hover {
  background-color: rgba(175, 147, 225, 0.7);
}
button.epic:focus {
  background-color: rgba(175, 147, 225, 0.7);
  border-color: #ffffff;
}
button.epic:active {
  background-color: rgba(205, 187, 236, 0.7);
}
button.rare {
  background-color: rgba(23, 174, 249, 0.7);
  border-color: #c8c8c8;
}
button.rare:hover {
  background-color: rgba(73, 191, 250, 0.7);
}
button.rare:focus {
  background-color: rgba(73, 191, 250, 0.7);
  border-color: #ffffff;
}
button.rare:active {
  background-color: rgba(122, 209, 252, 0.7);
}
button > * {
  margin: 5px;
}
button > h2 {
  font-size: 25px;
}
button > p {
  font-size: 15px;
}
button.danger {
  background-color: #821400;
  border-color: #b48c82;
  color: white;
}
button.danger:hover {
  background-color: #9b1800;
}
button.danger:focus {
  background-color: #9b1800;
  border-color: #ffffff;
}
button.danger:active {
  background-color: #b51c00;
}
.toastify {
  font-family: monospace;
  padding: 15px;
  position: fixed;
  border-radius: 3px;
  min-width: 200px;
  opacity: 0;
  z-index: 998;
  background: rgba(0, 0, 0, 0.75);
  color: rgba(237, 237, 237, 0.75);
  border: 1px solid rgba(237, 237, 237, 0.75);
}
.toastify span {
  display: block;
}
.toastify .toastify-title {
  font-weight: bold;
  margin-bottom: 10px;
}
.toastify .toastify-cancel-icon {
  display: none;
}
.toastify.toastify-bottom-right {
  bottom: 15px;
  right: 15px;
}
.toastify.toastify-top-right {
  top: 15px;
  right: 15px;
}
.toastify.toastify-top-left {
  top: 15px;
  left: 15px;
}
.toastify.toastify-bottom-left {
  bottom: 15px;
  left: 15px;
}
:root {
  color-scheme: dark;
  accent-color: #f0efef;
}
.full {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.hidden {
  opacity: 0;
}
.no-display {
  display: none;
}
* {
  font-family: "Space Mono", monospace;
}
h1,
h2,
h3,
h4,
h5,
h6,
img,
button,
.no-select {
  user-select: none;
}
canvas {
  margin: 0;
  padding: 0;
  display: block;
  cursor: none;
}
body {
  margin: 0;
  padding: 0;
  background-color: black;
}
h3 {
  margin-bottom: 0;
}
.secondary {
  color: #c8c8c8;
}
.warning {
  color: #f6ec1f;
}
.warning::before {
  font-family: "Font Awesome 6 Pro";
  content: "\f071";
  margin-right: 5px;
}
.danger {
  color: #821400;
}
.highlight {
  color: #fbc02d;
}
input:focus {
  outline: 1px solid #f0f0f0;
}
dialog {
  max-height: 75%;
  min-width: 25%;
  text-align: center;
  border: 3px solid #c8c8c8;
  border-radius: 3px;
  background-color: black;
  color: white;
  outline: none;
}
dialog::backdrop {
  background-color: rgba(0, 0, 0, 0.7);
}
dialog > h1 {
  font-size: 30px;
}
dialog > h2 {
  font-size: 25px;
}
.menu {
  width: 350px;
  padding: 20px;
  padding-top: 0px;
}
.centered {
  display: flex;
  width: 100%;
  align-items: center;
  flex-direction: column;
  flex-wrap: nowrap;
}
#controls {
  text-align: left;
}
#controls > span {
  vertical-align: middle;
  display: flex;
  justify-content: space-between;
}
#controls > span > span {
  display: flex;
}
#controls > span > span:last-child {
  margin-left: 10px;
}
#controls > span img {
  height: 1.5em;
  padding: 0;
  margin: 0;
}
#currentUpgrades {
  position: fixed;
  bottom: 0;
  left: 0;
  border: 2px solid #c8c8c8;
  padding: 5px;
  background-color: black;
  text-align: left;
}
#currentUpgrades > p {
  margin: 7px;
  font-weight: 600;
}
#currentUpgrades > div {
  padding: 5px;
  padding-left: 15px;
  margin: 0;
}
#currentUpgrades > div > p {
  padding: 3px;
  margin: 0;
  display: flex;
  justify-content: space-between;
}
#currentUpgrades > div > p > span {
  font-weight: 500;
  margin-left: 10px;
}
a {
  color: white;
  font-size: 15px;
  margin: 5px;
}
.vignette-red {
  width: 100vw;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
  background: radial-gradient(circle, rgba(23, 23, 23, 0) 0%, rgba(23, 23, 23, 0) 50%, rgba(194, 0, 0, 0.58) 150%);
  z-index: 999;
  pointer-events: none;
  transition: opacity 0.5s;
  opacity: 1;
}
#stats {
  text-align: justify;
}
#runData {
  width: 300px;
  min-width: unset;
  max-width: unset;
}
#runData > div > div > div {
  display: flex;
  justify-content: space-between;
  padding-bottom: 0;
  margin-bottom: 0;
}
#runData > div > div > div > strong {
  order: 1;
  margin-right: 20px;
}
#runData > div > div > div > span {
  order: 2;
}
#runData > div > div h3 {
  margin-top: 10px;
  text-align: left;
  border-bottom: 1px solid rgba(180, 180, 180, 0.5);
  margin-bottom: 5px;
}
#runData > div > div h3:first-child {
  margin-top: 0;
}
#runDataClose {
  margin-top: 20px;
  margin-bottom: 0;
}
#controls {
  margin-bottom: 5px;
  margin-top: 5px;
  padding: 5px 10px;
}
#controls h3 {
  text-align: center;
  margin-top: 0;
  margin-bottom: 5px;
}
ul li {
  text-align: left;
}
